.article{font-family:"Manrope"}.article .article-item{margin-bottom:70px}.article .article-item .box{width:100%;height:500px}.article .article-item .box img{width:100%;height:500px;object-fit:cover}.article .article-item .article-title{font-family:"Syne",Sans-serif;font-size:36px;font-weight:700;line-height:50px}.article .article-item .article-description{color:#555;font-size:16px;line-height:22px;font-weight:500;font-family:Manrope;margin-top:25px;margin-bottom:25px}.article .article-item p{font-size:16px;line-height:1.75;color:#111;font-family:"Manrope";margin:10px 0}.article .article-item p:first-child{margin-top:20px}.article .article-item h3{font-family:"Syne",Sans-serif;font-size:25px}.article .article-item .quote{background-color:rgba(243,248,251,.6);color:#555;text-align:center;padding:50px;line-height:1.3em;font-weight:600;font-size:20px;font-family:"Manrope";position:relative}.article .article-item .quote::before{content:"";position:absolute;top:80px;left:40px;font-size:250px;font-weight:700;color:rgba(255,53,36,.1)}.under-title-article{font-size:14px;display:flex;align-items:center;margin-top:10px;color:#797171}.under-title-article .icons{color:#bd9b84;margin-right:10px}.under-title-article .date{margin-right:30px}.article-category{width:100px;background-color:#fd2a42;padding:6px 22px;margin-bottom:5px;margin-top:50px;text-align:center}.article-category a{text-decoration:none;color:#fff;font-size:14px;line-height:21px;font-weight:500;font-family:Manrope}