.reviews {
    margin-bottom: 60px;
    .reviews-heading {
        font-family: 'Syne', Sans-serif;
        font-size: 24px;
        font-weight: 700;
    }
    .review-card {
        background: rgba(243, 248, 251, 0.6);
        .review-card-text {
            font-family: 'Manrope';
            font-size: 16px;
            span {
                font-weight: bolder;
            }
        }
    }

    .review-form {
        padding: 0 30px 30px 30px;
        background: rgba(243, 248, 251, 0.6);
        display: flex;
        flex-direction: column;
        // margin-top: 32px;
        border-radius: 15px;
        h3 {
            font-family: 'Syne', Sans-serif;
            font-size: 24px;
            font-weight: 700;
            border-bottom: 1px solid #e4e4e4;
        }
        .review-name,
        .review-text {
            width: 50%;
            padding: 10px;
            margin-bottom: 10px;
            outline: none;
            color: #495057;
            background-color: #fff;
            border: 1px solid #ced4da;
            font-family: 'Manrope';
            font-size: 16px;
        }
        .review-text {
            width: 100%;
            height: 100px;
        }
        .review-btn {
            color: #fff;
            border-radius: 22px;
            font-size: 15px;
            font-weight: 500;
            text-align: center;
            padding: 16px 28px;
            background-color: #bd9b84;

            border: none;
            cursor: pointer;
        }
    }
}
