.row {
    display: flex;

    .col-1 {
        width: 66%;
    }
    .col-2 {
        width: 33%;
        padding: 20px;
    }
}

.swiper {
    width: 100%;
    height: 400px;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.banner {
    margin-top: 50px;
    text-align: center;
    margin-bottom: 100px;
}

.popular-up {
    margin-bottom: 30px;
    display: flex;
    align-items: center;

    .popular-up-heading {
        font-family: Syne;
        font-size: 30px;
        font-weight: 700;
        color: #000;
    }
    .popular-arrows {
        position: relative;
        .left-up {
            position: absolute;
            top: -10px;
            left: 20px;
            margin-right: 10px;
            color: #555555;
            cursor: pointer;
        }
        .left-up:hover {
            color: rgb(189, 155, 132);
        }
        .right-up {
            position: absolute;
            top: -10px;
            left: 50px;
            margin-right: 10px;
            color: #555555;
            cursor: pointer;
        }
        .right-up:hover {
            color: rgb(189, 155, 132);
        }
    }
}
