.categories {
    margin-bottom: 45px;
    .categories-heading {
        display: flex;
        align-items: center;
        margin-bottom: 35px;
        .categories-title {
            color: #000;
            font-size: 28px;
            font-weight: 600;
            font-family: Manrope;
        }
        .categories-line {
            width: 45px;
            height: 5px;
            background-color: #bd9b84;
            margin-left: 20px;
        }
    }

    .categories-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .categories-name {
            text-decoration: none;
            font-size: 15px;
            font-weight: 500;
            font-family: Manrope;
            margin-bottom: 10px;
        }
        .categories-name a {
            color: #555555;
        }
        .categories-name:hover a {
            color: #bd9b84;
        }

        .categories-quantity {
            height: 25px;
            width: 25px;
            border-radius: 50%;
            background-color: #bd9b84;
            color: white;
            font-size: 13px;
            line-height: 25px;
            text-align: center;
            font-weight: 400;
            font-family: Manrope;
            margin-bottom: 10px;
        }
    }
}
