.under-title-author {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #797171;
    img {
        border-radius: 50%;
    }
    .author {
        font-size: 16px;
        font-weight: 600;
        color: #bd9b84;
        margin-left: 10px;
        margin-right: 45px;
    }
}

.about-author {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(243, 248, 251, 0.6);
    padding: 30px;
    border-radius: 20px;
    img {
        border-radius: 50%;
        margin-right: 25px;
    }

    .about-author-right {
        h6 {
            color: #555555;
            font-weight: 500;
            font-size: 15px;
            margin: 0px;
            font-family: 'Syne';
        }
        h4 {
            color: #424242;
            margin: 10px 0;
            font-size: 20px;
            line-height: 26px;
            font-weight: 600;
            font-family: 'Syne';
        }
        p {
            font-size: 16px;
            line-height: 24px;
            color: #000;
            margin: 0px;
            font-weight: 400;
            font-family: 'Manrope';
        }
    }
}
